<template>
  <header>
  </header>
  <div class="wrapper">
    <aside>
      <ul>
        <li><a href="javascript:void(0)" @click="scrollToSection('cognitiva')">Discapacidad cognitiva</a></li>
        <li><a href="javascript:void(0)" @click="scrollToSection('motora')">Discapacidad motora</a></li>
        <li><a href="javascript:void(0)" @click="scrollToSection('visualP')">Discapacidad visual parcial</a></li>
        <li><a href="javascript:void(0)" @click="scrollToSection('visualT')">Discapacidad visual total</a></li>
        <li><a href="javascript:void(0)" @click="scrollToSection('auditiva')">Discapacidad auditiva</a></li>
      </ul>
    </aside>
    <main>
      <h2>Simulación de barreras</h2>
      <p>
        Esta simulación interactiva te permite experimentar las barreras que enfrentan las personas con diferentes tipos de discapacidades al navegar por internet.
      </p>
      <p>Atrévete a ver el mundo desde otra perspectiva. </p>
      <section id="cognitiva">
        <h3>¿Qué desafíos enfrentan las personas con discapacidades en la web?</h3>
        <div id="text-container">
          <span class="line">La accesibilidad digital asegura que todas las personas puedan interactuar con tecnología sin barreras.</span>
          <span class="line">A pesar de los avances, muchas plataformas aún limitan la experiencia de usuarios con necesidades diversas.</span>
          <span class="line">Las barreras comunes incluyen interfaces desordenadas, controles pequeños, falta de retroalimentación y ausencia de navegación alternativa.</span>
          <span class="line">Estas dificultades afectan tanto a personas con discapacidades como a usuarios en situaciones temporales o contextos específicos, como luz solar, ruido o dispositivos de baja calidad.</span>
        </div>
      </section>

      <img id="kitten" src="../assets/kitten.jpg" alt = "Felicidades, has encontrado al gatito invisible. Nadie puede saber que está ahí excepto los usuarios con lectores de pantalla. No puedes verlo, pero te aseguramos que es adorable.">

      <section class= "d-visualp" id="visualP">
        <div class="frustrating-experience">
          <p>Para superar estos retos, es necesario que los desarrolladores y diseñadores adopten enfoques centrados en la accesibilidad. Esto incluye garantizar que la información esté disponible en múltiples formatos, crear interfaces claras y fáciles de entender, y ofrecer compatibilidad con dispositivos de asistencia.</p>
          <p>El beneficio de un diseño accesible no se limita a una pequeña parte de la población. Por el contrario, mejora la experiencia para todos los usuarios al hacer que la tecnología sea más intuitiva, flexible y amigable. </p>
        </div>
        <div class=color-blind>
        <div :class="{'filtro-bn': filtroActivo}" class="cuadro">
      Este texto no tiene contraste. Si bien más o menos se puede llegar a leer debido a los colores, en escala de grises es imposible. Esto resulta muy problemático para personas con daltonismo o con ciertas discapacidades visuales.
        </div>
        <br />
        <button class="bn" @click="toggleFiltro">Aplicar filtro</button>
        </div>
      </section>
    
          <section class="d-auditiva" id="auditiva">
        <video controls>
          <source src="../assets/sign.mp4" type="video/mp4">
          Tu navegador no soporta la reproducción de video.
        </video>
      </section>

      <div class="cursor-area" @mousemove="updateCursor" id="motora">
    <div ref="crazyCursor" class="crazy-cursor"></div>
    <p>Se suele pasar por alto que para las personas con discapacidades motoras, navegar por la web puede ser complicado. Mientras que para quienes usan el ratón es sencillo hacer clic en botones o enlaces, para ellos esto puede ser casi imposible. Si no pueden mover el ratón con precisión, tareas como llenar formularios o navegar entre páginas se vuelven muy difíciles. </p>
    <form>
        <label for="texto">Escribe aquí:</label><br>
        <textarea id="texto" name="texto" rows="1" ></textarea><br><br>
        <button type="submit">Enviar</button>
        <div class="contador">
        <p>Tiempo restante: {{ tiempoRestante }} segundos</p>
        </div>
    </form>
  </div>

      <section>
        <div class="invisible-content" id="visualT">
          <h2 class="visual-title">¿Hay algo más aquí?</h2>
          <p class="teaser-text">Parece que todo está claro... ¿o no? 🤔</p>

          <div aria-live="polite" class="hidden-message">
            <h3>Mensaje para usuarios de lectores de pantalla:</h3>
            <p>Has encontrado una sección oculta diseñada para demostrar la importancia de la accesibilidad.</p>
            <ul>
              <li>Este contenido es accesible solo mediante tecnologías asistivas.</li>
              <li>Recuerda que todos merecen una experiencia digital inclusiva.</li>
              <li>La accesibilidad beneficia a toda la sociedad.</li>
            </ul>
          </div>
        </div>
      </section>
      <section>
        <h3>Reflexión</h3>
        <p>Esta simulación de barreras es una manera muy interesante de ponernos en los zapatos de las personas con diferentes discapacidades al navegar por la web. Al interactuar con las barreras que presentan los distintos tipos de discapacidades, como la falta de contraste para personas con daltonismo o el cursor errático para quienes tienen dificultades motoras, se hace más evidente lo importante que es tener en cuenta la accesibilidad en el diseño web.</p>
        <p>Es impactante cómo muchas veces no pensamos en estos problemas hasta que los vivimos, pero la realidad es que las personas con discapacidades se enfrentan a estos obstáculos todo el tiempo. Además, la accesibilidad no solo ayuda a quienes tienen discapacidades permanentes, también puede ser útil para cualquiera en situaciones temporales, como cuando estamos en un entorno ruidoso o usando un dispositivo con poca capacidad.</p>   
        <p>La clave de todo esto es entender que un diseño accesible no es solo para unos pocos, sino que mejora la experiencia de todos. Facilitar la navegación, hacerla más clara y amigable, y asegurarse de que sea compatible con dispositivos de asistencia, no solo es lo correcto, sino que también beneficia a todos los usuarios.</p>     
      </section>
    </main>
  </div>
</template>

<script>


export default {
  name: 'SimulationView',

  mounted() {
    this.animateText();
    this.iniciarContador();  // Inicia el contador automáticamente
  },

  data() {
    return {
      sequence: [3, 1, 2, 0],
      index: 0,
      filtroActivo: false,
      tiempoRestante: 20,  // Tiempo inicial del contador
      intervalo: null,  // Para guardar el intervalo
    };
  },

  methods: {
    animateText() {
      const lines = document.querySelectorAll(".line");

      setInterval(() => {
        lines.forEach((line) => {
          line.style.opacity = "0";
        });

        const currentLine = this.sequence[this.index];
        lines[currentLine].style.transitionDelay = "0s";
        lines[currentLine].style.opacity = "1";

        this.index = (this.index + 1) % this.sequence.length;
      }, 2000);
    },
    updateCursor(event) {
    const cursor = this.$refs.crazyCursor;
  
  // Obtenemos la posición relativa del contenedor
    const cursorArea = document.querySelector('.cursor-area');
    const rect = cursorArea.getBoundingClientRect();
  
  // Calculamos las posiciones en relación al contenedor
    let x = event.clientX - rect.left;
    let y = event.clientY - rect.top;
  
  // Agrega un valor aleatorio para hacer el movimiento errático
    const randomOffsetX = (Math.random() - 0.5) * 100; // Aleatorio entre -25 y 25 píxeles en X
    const randomOffsetY = (Math.random() - 0.5) * 100; // Aleatorio entre -25 y 25 píxeles en Y
  
    x += randomOffsetX;
    y += randomOffsetY;
  
    // Ajusta las posiciones para centrar el cursor
    const cursorWidth = 16; // Ancho total del cursor (la suma de las bordes izquierdo y derecho)
    const cursorHeight = 16; // Alto del cursor
  
    cursor.style.left = `${x - cursorWidth / 2}px`;
    cursor.style.top = `${y - cursorHeight / 2}px`;
    },
    toggleFiltro() {
      this.filtroActivo = !this.filtroActivo;
    },
    scrollToSection(id) {
    const element = document.getElementById(id);
    if (element) {
      element.scrollIntoView({ behavior: "smooth" });
    }
  },
      iniciarContador() {
      this.intervalo = setInterval(() => {
        if (this.tiempoRestante > 0) {
          this.tiempoRestante--; // Decrementa el tiempo
        } else {
          clearInterval(this.intervalo); // Detiene el contador cuando llega a 0
          alert('¡El tiempo ha terminado!');
        }
      }, 1000);
    },

  // Limpiar el intervalo al destruir el componente
  beforeDestroy() {
    if (this.intervalo) clearInterval(this.intervalo);
  }
  },
};
</script>

